),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("L’application Yéti est elle suffisante pour préparer une course ?")]),_vm._v(" "),_c('p',[_vm._v("\n      L’application Yéti doit être utilisée en complément d’une analyse de risque menée à l’aide de la grille\n      d’analyse 3X3. Cette grille d’analyse permet d’analyser les 3 principaux facteurs de risque (conditions, terrain\n      et groupe) à trois moment clés du déroulement d’une course : pendant la préparation à la maison, pendant\n      l’approche et au pied des passages clés.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      On conservera toujours un œil critique sur les informations données par l’application (lire infra : Quelles sont\n      les limites de Yéti ?).\n    ")])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Quel est l’intérêt de Yéti ?")]),_vm._v(" "),_c('p',[_vm._v("\n      L’art de l’alpinisme et du ski de montagne est de parcourir un milieu potentiellement dangereux en réduisant les\n      risques.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Lors de la préparation à la maison, Yéti permet de distinguer d’un coup d’œil les pentes les plus risquées et\n      d’identifier facilement les passages clés d’un itinéraire. Valider un choix de course et imaginer des options de\n      repli sont facilités grâce à la vision d’ensemble qu’offre l’interface cartographique.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Avec Yéti, la préparation de course consiste à se faufiler dans les espaces de moindre risque, à privilégier les\n      pentes dont les probabilités de déclenchement sont les plus faibles (mais jamais nulles) ou, pour les plus\n      téméraires, à prendre des risques en connaissance de cause.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Yéti ne délivre pas un feu vert ou un feu rouge. Yéti donne une information sur la plus ou moins grande\n      probabilité de déclenchement des pentes afin d’orienter ses choix de course en fonction des conditions\n      nivologiques prévues.\n    ")])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("L’application Yéti peut-elle être utilisée sur le terrain ?")]),_vm._v(" "),_c('p',[_vm._v("Yéti est un outil d’aide à la décision pour préparer une course à la maison.")]),_vm._v(" "),_c('p',[_vm._v("\n      Sur le terrain, le pratiquant dispose d’informations nouvelles et concrètes sur les conditions, le terrain et le\n      groupe. Au fur et à mesure de la progression, l’analyse des données réelles confirme, nuance ou modifie les\n      hypothèses émises à la maison à l’aide de Yéti. Des outils comme le 3X3, la quantité critique de neige fraîche,\n      les signaux d’alarme, les bâtons pour mesurer la pente sont alors plus utiles que l’application Yéti pour\n      prendre une décision.\n    ")]),_vm._v(" "),_c('p',[_vm._v("Sur le terrain, les bonnes questions à se poser sont notamment :")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("y a-t-il des signaux d’alarme ?")]),_vm._v(" "),_c('li',[_vm._v("la quantité critique de neige fraîche est-elle dépassée ?")]),_vm._v(" "),_c('li',[_vm._v("la visibilité permet-elle de conduire une analyse de risque perspicace ?")]),_vm._v(" "),_c('li',[_vm._v("la configuration du terrain est-elle favorable ou défavorable ?")]),_vm._v(" "),_c('li',[_vm._v("est-il possible de faire une trace sûre ?")]),_vm._v(" "),_c('li',[_vm._v("les participants sont-ils motivés et au niveau pour la course ?")]),_vm._v(" "),_c('li',[_vm._v("etc")])]),_vm._v(" "),_c('p',[_vm._v("\n      Des réponses lucides à ces questions sont nécessaires pour prendre une bonne décision à l’approche et dans les\n      passages clés. Cela demande une formation solide et quelques années de pratique.\n    ")])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Quelles sont les limites de Yéti ?")]),_vm._v(" "),_c('p',[_vm._v("\n      Les principales limites de cet outil tiennent à celles des piliers sur lesquels ils reposent : le bulletin\n      d’avalanche, le modèle numérique de terrain, les méthodes de réduction et l’algorithme utilisé.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Les limites de la carte des pentes : la réalisation de la carte des pentes s’appuie sur les données de la base\n      RGEAlti de l’IGN. Cette base de données est issue d’un assemblage de différents modèles numérique de terrain\n      (MNT) dont la résolution est comprise entre 1 et 25 mètres. Toutes les pentes à plus de 30 degrés dont la taille\n      est supérieure à 25 mètres sur le terrain figurent sur la carte. Cette précision est la plupart du temps\n      suffisante pour évaluer l’inclinaison des passages clés d’un itinéraire à skis ou à raquettes. Toutefois, dans\n      certaines circonstances, des pentes réduites de moins de 25 mètres peuvent déjà être dangereuses. Il convient de\n      rester vigilant sur le terrain en conservant un esprit critique par rapport aux informations données par Yéti.\n      En outre, quelques aberrations visuelles peuvent survenir au niveau des frontières. Elles sont liées à la\n      qualité du découpage du MNT dans les zones frontalières.\n    ")]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"Aberration frontalière","src":require("@/assets/img/yeti/artefact_vallorcine.jpg")}})]),_vm._v(" "),_c('p',[_vm._v("\n      Pour en savoir plus :\n      "),_c('a',{attrs:{"href":"https://www.petzl.com/fondation/projets/carte-pente-risque-avalanches?language=fr#.Wi5q1lSdVE4"}},[_vm._v("Les pentes à plus de 30 degrés sur le Géoportail de l’IGN, Utilisation, intérêts et limites pour les amateurs\n        de sport de neige")])]),_vm._v(" "),_c('p',[_vm._v("\n      Les limites du bulletin d’avalanche : le bulletin d’avalanche dresse un état des lieux du danger d’avalanche à\n      l’échelle d’un massif en dehors des pistes balisées. Les conditions d’une vallée (et encore moins celle d’une\n      pente) ne sont pas évaluées par le bulletin d’avalanche. L’indice de danger de 1 à 5 est le résumé d’une\n      situation plus complexe décrite dans le texte qui accompagne le bulletin. La lecture de ce texte est l’un des\n      piliers de l’analyse de risque avec la grille 3X3, laquelle doit systématiquement précéder l’usage de Yéti. La\n      saisie de l’indice de danger dans le formulaire de Yéti suppose que le pratiquant a bien pris connaissance de\n      l’ensemble du bulletin : texte, rose des vents, quantité récente de neige fraîche… Enfin, le bulletin n’est\n      diffusé en France que du 15 décembre au 30 avril alors que l’exposition au danger peut avoir lieu toute l’année\n      notamment en haute montagne. Il existe des outils pour affiner et, au besoin, corriger les informations du\n      bulletin d’avalanche. Se reporter aux ouvrages cités dans la question 1.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Les limites des méthodes de réduction : les méthodes de réduction s’appuient sur de nombreuses expériences\n      sédimentées à travers l’accidentologie dont on peut tirer quelques enseignements par le biais des probabilités.\n      Cette somme d’expériences sera toujours plus étendue que l’expérience d’une seule personne, fût-elle chevronnée.\n      Toutefois, il demeure possible de provoquer ou de subir une avalanche y compris dans une pente non identifiée\n      comme «à risque» par une méthode de réduction. Le pratiquant de la montagne enneigée doit faire un usage\n      raisonné et critique de Yéti et des outils mis à sa disposition pour évaluer le risque. Il reste responsable des\n      analyses conduites à la maison, des décisions prises sur le terrain, de ses actes et de leurs conséquences. Les\n      auteurs et institutions partenaires du projet ne sauraient être tenus responsables des accidents survenant lors\n      de la pratique après utilisation de l’application Yéti.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      Les limites de l’algorithme utilisé par Yéti : en l’état actuel du développement, seules les pentes considérées\n      comme «à risque» par les méthodes de réduction (MRD, MRE ou MRP) sont affichées avec un estompage coloré. Or, à\n      partir du danger 3, il convient de prendre en compte les pentes raides qui dominent l’itinéraire envisagé car\n      elles sont susceptibles d’être déclenchées à distance. À partir du danger 3, l’absence d’estompage ne signifie\n      pas que l’itinéraire est sûr. Vérifier les pentes dominant l’itinéraire et s’assurer qu’elles ne menacent pas\n      directement la trace.\n    ")]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"Prise en compte des pentes amont","src":require("@/assets/img/yeti/pentes_amont.jpg")}})]),_vm._v(" "),_c('p',[_vm._v("\n      Dans l’exemple ci-dessous, par risque 3, si on remonte le vallon en direction du lac, on est en permanence\n      dominé par des pentes considérées comme à risque (bleue foncée rive droite, bleue clair rive gauche). Un départ\n      d’avalanche à partir du danger 3 peut s’écouler en terrain moins raide sur lequel il n’y a pas d’estompage de\n      couleur.\n    ")])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Comment choisir une méthode de réduction adaptée à sa pratique ?")]),_vm._v(" "),_c('h4',{staticClass:"title is-4"},[_vm._v("La méthode de réduction débutant (MRD)")]),_vm._v(" "),_c('p',[_vm._v("\n      La méthode de réduction débutant (MRD) s’adresse aux pratiquants possédant peu d’expérience de la neige en\n      dehors des pistes balisées. Un encadrant expérimenté préparant une sortie pour des débutants aura intérêt à\n      utiliser cette méthode afin de se ménager une marge de sécurité adaptée à un public qui progresse plus lentement\n      avec des chutes fréquentes (sollicitation du manteau neigeux). Les zones considérées comme à risque sont\n      figurées en bleu clair.\n    ")]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"MRD","src":require("@/assets/img/yeti/mrd.jpg")}})]),_vm._v(" "),_c('h4',{staticClass:"title is-4"},[_vm._v("La méthode de réduction élémentaire (MRE)")]),_vm._v(" "),_c('p',[_vm._v("\n      La méthode de réduction élémentaire (MRE) convient aux pratiquants expérimentés soucieux de conserver une marge\n      de sécurité confortable. Avec la MRE, on peut combler une belle vie de skieur en profitant de la poudreuse en\n      hiver et des meilleures neiges de printemps. Elle a été adoptée par des guides expérimentés qui l’estiment\n      suffisante pour se faire plaisir en exerçant leur profession de manière plus détendue.\n    ")]),_vm._v(" "),_c('p',[_vm._v("Les zones considérées comme à risque sont figurées par deux tons de bleu :")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("bleu foncé pour les pentes à risque dans les orientations cochées sur la rose des vents.")]),_vm._v(" "),_c('li',[_vm._v("bleu clair pour les autres pentes à risque")])]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"MRE","src":require("@/assets/img/yeti/mre.jpg")}})]),_vm._v(" "),_c('h4',{staticClass:"title is-4"},[_vm._v("La méthode de réduction professionnelle (MRP)")]),_vm._v(" "),_c('p',[_vm._v("\n      La méthode de réduction professionnelle (MRP) offre un choix de course plus important sur le terrain notamment\n      en pente raide, mais réduit la marge de sécurité. Elle nécessite une compréhension plus fine du manteau neigeux\n      et fait intervenir des paramètres comme l’orientation de la pente, la fréquentation de l’itinéraire et les\n      mesures de précaution envisagée pendant la course. Elle s’adresse aux pratiquants expérimentés qui se sentent\n      trop limités par la MRE.\n    ")]),_vm._v(" "),_c('p',[_vm._v("Les pentes à risque sont figurées par deux couleurs :")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("risque résiduel inférieur à 1 : pas d’estompage de couleur")]),_vm._v(" "),_c('li',[_vm._v("risque résiduel compris entre 1 et 1.5 : mauve")]),_vm._v(" "),_c('li',[_vm._v("risque résiduel > 1.5 : violet")])]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"MRP","src":require("@/assets/img/yeti/mre.jpg")}})])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Comment utiliser Yéti avec la MRD ?")]),_vm._v(" "),_c('p',[_vm._v("\n      Sélectionner la méthode de réduction débutant (MRD) dans le formulaire. Saisir le niveau de danger annoncé par\n      le bulletin d’avalanche, de 1 à 3. Le degré 4 ne peut être saisi dans la MRD. Le cas échéant, mentionner la\n      limite d’altitude à partir de laquelle le danger évolue. Zoomer sur le massif, la vallée ou la pente concernée.\n      Activez la carte IGN en cliquant sur l’icône bleue en bas à gauche de la carte. Soumettre la requête en cliquant\n      sur \"Calculer le risque\". L’application affiche les pentes à risque avec un code couleur sur une interface\n      cartographique. La légende est accessible en cliquant sur l’icône « point d’interrogation » en haut à droite de\n      la carte.\n    ")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("\n        Pas d’estompage de couleur : le projet est valable. Rester vigilant sur le terrain si les conditions ou la\n        taille du groupe évoluent. Attention aux pentes qui dominent l’itinéraire à partir du danger 3 (risque de\n        déclenchement à distance).\n      ")]),_vm._v(" "),_c('li',[_vm._v("\n        Couleur bleu clair : pentes à risque, à éviter. À partir du danger 3, prendre en compte les pentes colorées en\n        bleu qui dominent l'itinéraire.\n      ")])]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"MRD","src":require("@/assets/img/yeti/mrd_carte.jpg")}})])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Comment utiliser Yéti avec la MRE ?")]),_vm._v(" "),_c('p',[_vm._v("\n      Sélectionner la méthode de réduction élémentaire (MRE) dans le formulaire. Saisir le niveau de danger annoncé\n      par le bulletin d’avalanche, de 1 à 4. Le cas échéant, mentionner la limite d’altitude à partir de laquelle le\n      danger évolue. Signaler les orientations critiques données dans la rose des vents du bulletin. Zoomer sur le\n      massif, la vallée ou la pente concernée. Activez la carte IGN en cliquant sur l’icône bleue en bas à gauche de\n      la carte. Soumettre la requête en cliquant sur \"Calculer le risque\". L’application affiche les pentes à risque\n      avec un code couleur sur une interface cartographique. La légende est accessible en cliquant sur l’icône « point\n      d’interrogation » en haut à droite de la carte.\n    ")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("\n        Pas d’estompage de couleur : le projet est valable. Rester vigilant sur le terrain si les conditions ou la\n        taille du groupe évoluent. Attention aux pentes qui dominent l’itinéraire à partir du danger 3 (risque de\n        déclenchement à distance).\n      ")]),_vm._v(" "),_c('li',[_vm._v("Couleur bleu clair : pentes à risque")]),_vm._v(" "),_c('li',[_vm._v("Couleur bleu foncé : risque accru du fait de l’orientation")])]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"MRE","src":require("@/assets/img/yeti/mre_carte.jpg")}})])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Comment utiliser Yéti avec la MRP ?")]),_vm._v(" "),_c('p',[_vm._v("\n      Sélectionner la méthode de réduction professionnelle (MRP) dans le formulaire. Saisir le potentiel de danger de\n      1 à 12. Si vous estimez que le manteau neigeux sera mouillé, cochez la case «neige mouillée» (pluie, soleil et\n      chaleur notamment au printemps) Préciser la taille du groupe et les mesures de précaution envisagée. Zoomer sur\n      le massif, la vallée ou la pente concernée. Activez la carte IGN en cliquant sur l’icône bleue en bas à gauche\n      de la carte. Soumettre la requête en cliquant sur \"Calculer le risque\". L’application affiche les pentes à\n      risque avec un code couleur sur une interface cartographique. La légende est accessible en cliquant sur l’icône\n      « point d’interrogation » en haut à droite de la carte.\n    ")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("\n        Pas d’estompage de couleur : risque résiduel < inférieur à 1. Risque faible, relativement sûr en l'absence\n        d'autres indicateurs de danger. Rester vigilant sur le terrain si les conditions ou la taille du groupe\n        évoluent. Attention aux pentes qui dominent à partir du danger 3 (risque de déclenchement à distance).\n      ")]),_vm._v(" "),_c('li',[_vm._v("\n        Couleur mauve : risque résiduel entre 1 et 1.5. Risque accru, prudence, formation et expérience sont\n        nécessaires, afin d'adopter une attitude défensive en prenant des mesures de précaution pour réduire le\n        risque. Des conditions nivologiques locales plus sûres que prévu ou une configuration du terrain favorable\n        sont requises pour poursuivre sur cet itinéraire.\n      ")]),_vm._v(" "),_c('li',[_vm._v("Couleur violette : risque résiduel supérieur à 1.5. Risque élevé, mieux vaut renoncer à cette course.")])]),_vm._v(" "),_c('p',{staticClass:"image"},[_c('img',{attrs:{"alt":"MRP","src":require("@/assets/img/yeti/mrp_carte.jpg")}})])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"title"},[_vm._v("Qui sont les concepteurs et partenaires de l’application Yéti ?")]),_vm._v(" "),_c('p',[_vm._v("\n      L’application a d’abord été développée sous forme d’un démonstrateur sous le logiciel Qgis. Le développement a\n      été assuré par des élèves ingénieurs et des enseignants-chercheurs de l’"),_c('a',{attrs:{"href":"http://www.ensg.eu"}},[_vm._v("ENSG-Géomatique")]),_vm._v("\n      ainsi que par des développeurs du Géoportail de l’IGN.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      La "),_c('a',{attrs:{"href":"https://www.petzl.com/fondation/fondation-petzl?language=fr"}},[_vm._v("Fondation Petzl")]),_vm._v(" accompagne et\n      soutient le projet